//767 :  UI

const WindowDimensions = () => { //    //  ,  1920,  500
  const [windowDimensions, setWindowDimensions] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  useEffect(() => {
    const handleResize = () => {
      setWindowDimensions({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return windowDimensions;
};

//   
const Layout = ({ children }) => {
  const { width } = WindowDimensions();

  return (
    <Container>
      {/* Header  */}
      <HeaderContainer>
        <Header />
        {/* {width} */}
      </HeaderContainer>

      {/* ,     */}
      {width <= 1024 && 
      <TopMenuContainer>
        <TopMenu />
      </TopMenuContainer>}

      {/*   */}
      <MainContent>
        {/* SideMenu  */}
        {width > 1024 && 
        <SideMenuContainer>
          <SideMenu />
        </SideMenuContainer>
        }

        {/*    */}
        <ScrollableContent>
          {children}
        </ScrollableContent>
      </MainContent>

      {/* Footer (Tail) */}
      <TailContainer>
        <Tail />
      </TailContainer>
    </Container>
  );
};

const App = () => {
  const { width } = WindowDimensions();

  const VisitorCount = async () => { // 
    const db = getDatabase();
    const visitorsRef = ref(db, "visitors"); // 
    const dailyVisitorsRef = ref(db, "dailyVisitors"); // ( )
  
    await get(visitorsRef).then(async (snapshot) => {
      if (snapshot.exists()) {
        //   1 
        await set(visitorsRef, snapshot.val() + 1);
      } else {
        // 
        await set(visitorsRef, 1);
      }
    });

    await get(dailyVisitorsRef).then(async (snapshot) => {
      if (snapshot.exists()) {
        //   1 
        await set(dailyVisitorsRef, snapshot.val() + 1);
      } else {
        // 
        await set(dailyVisitorsRef, 1);
      }
    });
  };
  


  useEffect(() => {
    VisitorCount();
  }, []);

  return (
    <div style={{textAlign: 'center'}}>
      <AuthProvider>
        <HashRouter>
          <Routes>
            <Route path="/" element={<Layout><List /></Layout>} />
            <Route path="post" element={<Layout><Post /></Layout>} />
            <Route path="list" element={<Layout><List /></Layout>} />
            <Route path="list/:id" element={<Layout><Detail /></Layout>} />
            <Route path="edit/:id" element={<Layout><Edit /></Layout>} />
            <Route path="signup" element={<Layout><SignUp /></Layout>} />
            <Route path="login" element={<Layout><Login /></Layout>} />
            <Route path="portfolio" element={<Layout><PortfolioHome /></Layout>} />
            <Route path="portfolio/post" element={<Layout><PortfolioPost /></Layout>} />
            <Route path="portfolio/edit" element={<Layout><PortfolioEdit /></Layout>} />
            <Route //Google SEO HTML  
              path="google5456435b0540cd8d.html"
              element={
                <meta
                  http-equiv="refresh"
                  content="0; url=https://hyeongjun513.github.io/blog/google5456435b0540cd8d.html"
                />
              }
            />
            <Route path="width" element={<div style={{marginTop:'10rem', fontSize:'3rem'}}><HeaderContainer><Header/></HeaderContainer> width  : {width}</div>} /> {/* width   */}
            <Route path="*" element={<ErrorPage />} />
          </Routes>
        </HashRouter>
      </AuthProvider>
    </div>
  );
};

