import React from "react";
import { BrowserRouter, Routes, Route, Link, useNavigate } from 'react-router-dom';
import styled from "styled-components";

const Header = () => {
    const navigate = useNavigate();

    return (
    <div>
        <div style={{display:'flex', flexDirection:'row', alignItems:'flex-end', justifyContent:'space-between', padding: '1rem 0 0 0'}}>
            <div style={{margin: 0}}>
                <Title style={{cursor: 'pointer'}} onClick={() => {navigate('/')}} >  </Title>
            </div>
            
            <div style={{display:'flex', flexDirection:'row', alignItems:'center'}}>
                <MenuButton onClick={() => {navigate('/')}}>Home</MenuButton>
                <MenuButton onClick={() => {navigate('/portfolio')}}>Portfolio</MenuButton>
            </div>
        </div>
        <HR />
    </div>
    );
};

export default Header;

const Title = styled.h1`
  font-family: "Nanum Pen Script", serif;
  font-weight: 500;
  font-style: normal;
  font-size: 2.5rem;
  margin: 0.5rem 0 0 0;

  @media (max-width: 1024px) {
    font-size: 1.8rem;
  }
`;

const MenuButton = styled.button`
  padding: 5px 10px;
  min-width: 50px;
  font-size: 1.2rem;
  background-color: white;
  border: none;
  margin: 0 0.1rem;
  cursor: pointer;
  font-family: "Merienda", serif;
  font-weight: 700;
  font-style: normal;
  color: black;

  position: relative; /* ::after  position  */
  &:hover {
    color: gray;
  }

  /*   */
  &::after {
    content: '';
    position: absolute;
    bottom: 0; /*     */
    left: 50%; /*   */
    width: 0%; /*   0% */
    height: 2px; /*   */
    background-color: gray; /*   */
    transform: translateX(-50%); /*   */
    transition: width 0.3s ease-out; /*    */
  }

  &:hover::after {
    width: 80%; /*       */
  }

  @media (max-width: 1024px) {
    font-size: 0.8rem;
  }
`;

const HR = styled.hr`
  height : 0.2rem;
  background-color : gray;
  border : 0;
  margin: 1rem 0 0 0;
`