import React, { useEffect, useState, useContext } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import { getDatabase, ref, get, set, push, remove } from "firebase/database";
import { getStorage, ref as storageRef, deleteObject } from "firebase/storage";
import { AuthContext } from "./AuthContext ";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import styled from "styled-components";

const Detail = () => {
  const { id } = useParams(); // URL ID 
  const [post, setPost] = useState(null);
  const [comments, setComments] = useState([]); //   
  const [nickname, setNickname] = useState("");
  const [password, setPassword] = useState("");
  const [comment, setComment] = useState("");
  
  const navigate = useNavigate(); //   List    
  const location = useLocation();

  const db = getDatabase(); // Realtime Database 
  const { currentUser } = useContext(AuthContext); //    
  const sortedPosts = location.state?.sortedPosts || []; //  
  const currentIndex = sortedPosts.findIndex((p) => p.id === id); //    
  // console.log('sortedPosts : ', sortedPosts);
  // console.log(' : ', currentIndex);
  const nextPost = currentIndex > 0 ? sortedPosts[currentIndex - 1] : null; // 
  const previousPost = currentIndex < sortedPosts.length - 1 ? sortedPosts[currentIndex + 1] : null; // 
  // console.log('previousPost : ', previousPost);
  // console.log('nextPost : ', nextPost);

  //   
  useEffect(() => {
    const fetchPost = async () => {
      const postRef = ref(db, "posts/" + id);
      const snapshot = await get(postRef);
      if (snapshot.exists()) {
        setPost(snapshot.val());
      } else {
        console.error("   .");
      }
    };

    //   
    const fetchComments = async () => {
      const commentsRef = ref(db, `comments/${id}`); //   
      const snapshot = await get(commentsRef);
      if (snapshot.exists()) {
        const data = snapshot.val();
        setComments(Object.entries(data).map(([key, value]) => ({ id: key, ...value })));
      } else {
        setComments([]); //       
      };
    };

    const incrementViewCount = async () => { //  (View+1)
      const postRef = ref(db, `posts/${id}/views`);
  
      await get(postRef).then(async (snapshot) => {
        if (snapshot.exists()) {
          await set(postRef, snapshot.val() + 1);
        } else {
          await set(postRef, 1); //    
        }
      });
    };

    fetchPost();
    fetchComments();
    incrementViewCount();
  }, [id]);

  //   
  const handleDelete = async () => {
    const storage = getStorage();

    if (window.confirm(" ?")) {
      try {
        const postRef = ref(db, "posts/" + id); // Database  
        const fileURL = post?.fileURL; // Storage   
  
        // Realtime Database  
        await remove(postRef);
  
        // Storage  
        if (fileURL) {
          const fileRef = storageRef(storage, fileURL); //  Storage  
          await deleteObject(fileRef); //  
        }
  
        alert(" !");
        navigate("/list");
      } catch (error) {
        console.error("    :", error);
        alert("  .");
      }
    }
  };

  //   
  const handleEdit = () => {
    navigate(`/edit/${id}`); //   
  };

  //   
  const handleAddComment = async () => {
    console.log('comments  : ', comments);
    console.log('comments  : ', comments.length);
    if (!nickname || !password || !comment) {
      alert(", ,    .");
      return;
    }

    const newComment = {
      nickname,
      password,
      content: comment,
      timestamp: Date.now(),
    };

    const commentsRef = ref(db, `comments/${id}`);
    const newCommentRef = push(commentsRef); //  ID   
    await set(newCommentRef, newComment);

    setNickname("");
    setPassword("");
    setComment("");

    alert(" !");
    //    
    setComments((prev) => [...prev, { id: newCommentRef.key, ...newComment }]);
  };

  //   
  const handleDeleteComment = async (commentId, commentPassword) => {
    const userPassword = prompt(" .");
    if (userPassword === commentPassword) {
      const commentRef = ref(db, `comments/${id}/${commentId}`);
      await remove(commentRef);

      alert(" !");
      setComments((prev) => prev.filter((c) => c.id !== commentId));
    } else {
      alert("  .");
    }
  };

  if (!post) {
    return <p>    .</p>;
  };

  return (
    <div style={{display:'flex', flexDirection:'column', alignItems:'center', minHeight: 'calc(100vh - 15rem)'}}>
      <div style={{width:'95%'}}>

        {/*   */}
        <div style={{display:'flex', flexDirection:'row', justifyContent:'space-between', margin:'0 0 0.2rem 0'}}>
          <div style={{display:'flex', flexDirection:'row', alignItems:'center'}}>
            <CategoryIcon alt="folder" src={`${process.env.PUBLIC_URL}/img/folder.png`}/>
            <Category>{post.category}</Category>
          </div>
          {currentUser ? //     
          <div style={{display:'flex', alignItems:'center', justifyContent:'center'}}>
            <Button_ED onClick={handleEdit}></Button_ED>
            <div style={{backgroundColor:'black', width:'0.05rem', height:'80%', margin:'0 0.2rem 0 0.2rem'}}> </div>
            <Button_ED onClick={handleDelete} style={{ color: "red" }}></Button_ED>
          </div>
          :
          '' }
        </div>
        <Title>{post.title}</Title>
        <div style={{display:'flex', flexDirection:'row', justifyContent:'space-between'}}>
          <Time style={{}}>: {post.uploadTime} &nbsp; &nbsp;</Time>
          {post.editTime && <Time>: {post.editTime}</Time>}
        </div>
        <HR />

        {/*   */}
        {/* <PostContent>{post.content}</PostContent> */}
        <PostContent>
          <ReactMarkdown children={post.content} remarkPlugins={[remarkGfm]} />
        </PostContent>
        
        {/*    */}
        <div style={{display:'flex', flexDirection:'row', justifyContent:'space-between', alignItems:'flex-end', marginTop:'4rem'}}>
          <div style={{display:'flex', flexDirection:'row', alignItems:'center', margin:'0'}}>
            <TailText>Posted By</TailText>
            <TailText style={{backgroundColor:'white', color: 'gray'}}></TailText>
          </div>
          <VisitorText>: {post.views}</VisitorText>
        </div>

        <hr style={{marginTop:'0.2rem'}}/>

        {/* ,   */}
        <div style={{display:'flex', flexDirection:'column', width:'100%', textAlign:'left', marginTop: '1rem'}}>

          <div style={{display:'flex', flexDirection:'row', alignItems:'center'}}>
            <CommentContainerIcon alt="page" src={`${process.env.PUBLIC_URL}/img/page.png`} style={{}}/>
            <TailTitle>  </TailTitle>
          </div>

          <div style={{display:'flex', flexDirection:'row', width:'100%'}}>
          {previousPost ? (
            <MovePostButton onClick={() => navigate(`/list/${previousPost.id}`, { state: { sortedPosts } })} style={{borderRight:'0'}}>
              <p style={{margin: '0', fontSize:'0.8rem', color:'gray'}}> </p>
              <MovePostText>{previousPost.title}</MovePostText>
            </MovePostButton>
          )
          :
          (<NoneDiv>
            <p>  .</p>
          </NoneDiv>
          )}
          {nextPost ? (
            <MovePostButton onClick={() => navigate(`/list/${nextPost.id}`, { state: { sortedPosts } })}>
              <p style={{margin: '0', fontSize:'0.8rem', color:'gray'}}> </p>
              <MovePostText>{nextPost.title}</MovePostText>
            </MovePostButton>
          )
          :
          (<NoneDiv>
            <p style={{margin:'0 0 0 0'}}>  .</p>
          </NoneDiv>
          )}
          </div>

        </div>
      
        {/*  */}
        <div style={{display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between', margin: '2rem 0 0 0'}}>
          <div style={{display:'flex', flexDirection:'row', alignItems:'center'}}>
            <CommentContainerIcon alt="comment" src={`${process.env.PUBLIC_URL}/img/comments.png`} />
            <TailTitle>({comments.length})</TailTitle>
          </div>
          {/* <VisitorText>: {post.views}</VisitorText> */}
        </div>
        
          {/*   */}
          <CommentListContainer>

            {comments.map((c) => (
              <CommentContainer key={c.id}>
                <div style={{display:'flex', flexDirection:'row', alignItems:'center'}}>
                  <CommentNickname>{c.nickname}</CommentNickname> {/*    */}
                  <CommentTime>{new Date(c.timestamp).toLocaleString()}</CommentTime> {/*     */}
                  {currentUser && <CommentTime style={{color:'black'}}>|  : {c.password}</CommentTime>} {/*       */}
                  <div style={{margin:0, display:'flex', alignItems:'center', justifyContent:'flex-end', flex:1}}>
                    <CommentDeletButton onClick={() => {handleDeleteComment(c.id, c.password)}}>X</CommentDeletButton> {/*    */}
                  </div>
                </div>
                <CommentContent>{c.content}</CommentContent>
              </CommentContainer>
            ))}

          </CommentListContainer>

          {/*   */}
          <CommentWriteContainer>

            <div style={{display:'flex', flexDirection:'row', padding:'0.3rem 0 0.3rem 0'}}>
              <CommentIcon alt="person" src={`${process.env.PUBLIC_URL}/img/person.png`} />
              <NicknameInput
                type="text"
                placeholder=" "
                value={nickname}
                style={{width:'10rem'}}
                maxLength={10}
                onChange={(e) => setNickname(e.target.value)}
              />

              <CommentIcon alt="lock" src={`${process.env.PUBLIC_URL}/img/lock.png`} />
              <NicknameInput
                type="password"
                placeholder=" "
                value={password}
                style={{width:'7rem'}}
                maxLength={7}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>

            <div style={{width:'100%', display:'flex', flexDirection:'column', alignItems:'flex-end'}}>
              <CommentTextArea
                placeholder=" ."
                spellCheck="false"
                value={comment}
                maxLength={500}
                onChange={(e) => setComment(e.target.value)}
              />
              <CommentButton onClick={handleAddComment}> </CommentButton>
            </div>

          </CommentWriteContainer>

        </div>

      {/* <div>
        <h3></h3>
        <ReactMarkdown children={post.content} remarkPlugins={[remarkGfm]} />
      </div> */}

      </div>
  );
};

export default Detail;

const Category = styled.p`
  // background-color: skyblue;
  text-align: left;
  margin: 0 0 0 -0.1rem;
  font-size: 1.2rem;
  color: #606060;
  font-weight: 600;

  font-family: "Song Myung", serif;
  font-style: normal;

  @media (max-width: 1024px) {
    font-size: 1rem;
  }
`

const CategoryIcon = styled.img`
  filter: opacity(0.6);
  width: 1.4rem;
  height: 1.4rem;
  margin-right: 0.2rem;

  @media (max-width: 1024px) {
    width: 1rem;
    height: 1rem;
  }
`;

const Title = styled.p`
  font-weight: bold;
  margin: 0.2rem 0.2rem 0.2rem -0.2rem; 
  text-align: left;
  font-size: 1.5rem;

  @media (max-width: 1024px) {
    font-size: 1.3rem;
  }
`;

const HR = styled.hr`
  height : 0.2rem;
  background-color : gray;
  border : 0;
`;

const Time = styled.p`
  display: flex;
  // flex-direction: row;
  // text-align: left;
  margin: 0.4rem 0.4rem 0 0; 
  color: gray; 
  font-weight: 600;
  font-size: 1rem;
  @media (max-width: 1024px) and (min-width: 501px) {
    font-size: 0.8rem;
  }
  @media (max-width: 500px) {
    font-size: 0.6rem;
  }
`;

const Button_ED = styled.button`
  border: 0px;
  margin: 0 0.3rem 0 0.3rem;
  cursor: pointer;
  font-size: 0.9rem;
  height: 100%;
  padding: 0rem 0.5rem 0rem 0.5rem;
  background-color: white;

  &:hover {
  // text-decoration: underline;
  background-color: lightgray;
  }

  @media (max-width: 1024px) {
    font-size: 0.7rem;
  }
`;

const PostContent = styled.p`
  font-size: 1.2rem;
  text-align: left;

  font-family: "Noto Sans KR", serif;
  font-style: normal;

  img {
    max-width: 100%; /*      */
    height: auto;   /*    */
  }

  @media (max-width: 1024px) {
    font-size: 1rem;
  }
`;

const NoneDiv = styled.div`
  width: 50%;
  border: 1px solid black;
  background-color: white;
  padding: 0.7rem;

  color: gray;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1rem;

  @media (max-width: 1024px) {
    font-size: 0.8rem;
  }
`;

const MovePostButton = styled.button`
  width: 50%;
  cursor: pointer;
  border: 1px solid black;
  background-color: white;
  padding: 0.7rem;

  &:hover {
  background-color: lightgray;
  }
`;

const MovePostText = styled.p`
  margin: 0.2rem 0 0 0;
  color: #2A408E;
  font-size: 1.2rem;

  display: -webkit-box; /*    */
  -webkit-line-clamp: 1; /*  1  */
  -webkit-box-orient: vertical; /*    */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /*     */

  @media (max-width: 1024px) {
    font-size: 1rem;
  }
`;

const CommentContainerIcon = styled.img`
  filter: opacity(0.8);
  width: 1.8rem;
  height: 1.9rem;
  margin: 0.2rem 0.2rem 0.4rem 0;

  @media (max-width: 1024px) {
    width: 1.4rem;
    height: 1.5rem;
  }
`;

const TailText = styled.p`
  font-size: 0.8rem;
  font-weight: 600;
  background-color: gray;
  color: white;
  padding: 0rem 0.5rem 0.2rem 0.5rem;
  border-radius: 10px;
  margin: 0;

  @media (max-width: 1024px) {
    font-size: 0.6rem;
  }
`

const TailTitle = styled.p`
  margin: 0 0 0.2rem 0;
  font-size: 1.7rem;

  font-family: "Yeon Sung", serif;
  font-weight: 500;
  font-style: normal;

  @media (max-width: 1024px) {
    font-size: 1.5rem;
  }
`;

const VisitorText = styled.p`
  margin: 0.3rem 0 0 0;
  font-size: 0.8rem;
  font-weight: bold;
  color: gray;

  @media (max-width: 1024px) {
    font-size: 0.6rem;
  }
`;

const CommentListContainer = styled.div`
  width: 100%;
  border: 1px solid black;
  border-radius: 15px;
  margin: 0 0 1rem 0;
`

const CommentContainer = styled.div`
  border-bottom: 2px solid lightgray;
  padding: 0 0 0.2rem 0;
  margin: 1rem;
`;

const CommentNickname = styled.p`
  margin: 0rem 0.5rem 0.2rem 0.5rem;
  font-size: 1.2rem;
  font-weight: bold;

  @media (max-width: 1024px) {
    margin: 0rem 0.3rem 0.2rem 0.5rem;
    font-size: 1rem;
  }
`;

const CommentTime = styled.p`
  margin: 0.2rem 0.2rem 0.2rem 0.5rem;
  color: gray;
  font-size: 0.8rem;

  font-family: "Noto Sans KR", serif;
  font-weight: bold;

  @media (max-width: 1024px) {
    margin: 0.2rem 0.1rem 0.2rem 0.3rem;
    font-size: 0.5rem;
  }
`;

const CommentDeletButton = styled.button`
  border: 0px;
  color: red;
  margin: 0;
  background-color: white;
  cursor: pointer;
  font-size: 0.9rem;

  @media (max-width: 1024px) {
    font-size: 0.7rem;
  }
`;

const CommentContent = styled.p`
  display: flex;
  flex-direction: row;
  text-align: left;
  margin: 0.2rem 0.5rem 0.2rem 0.5rem;
  font-size: 1rem;
  border-left: 2px solid gray;
  padding: 0 0 0 0.7rem;
  
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-style: normal;

  @media (max-width: 1024px) {
    font-size: 0.8rem;
    padding: 0 0 0 0.4rem;
  }
`;

const CommentWriteContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 2rem 0;
`

const NicknameInput = styled.input`
  font-size: 1rem;
  color: #222222;
  width: 10rem;
  border: none;
  border-bottom: solid #aaaaaa 1px;
  padding-bottom: 0.2rem;
  padding-left: 0.2rem;
  background: none;
  margin: 0 1rem 0 0;
  font-weight: bold;

  &:focus { 
    outline: none; 
  }

  @media (max-width: 1024px) {
    font-size: 0.8rem;
    width: 8rem;
  }
`;

const CommentTextArea = styled.textarea`
  width: 99%;
  height: 3rem;
  resize: none;
  font-size: 1rem;
  border: 1px solid gray;
  border-radius: 10px;
  outline: none;
  background-color: #F3F3F3;
  color: black;
  margin: 0.5rem 0.4rem 0.5rem 0;
  padding: 0.5rem 0 0.5rem 0.3rem;

  font-family: "Noto Sans KR", serif;
  font-weight: <weight>;

  &::placeholder { /* placeholder  */
    color: #919797; /*    */
    font-weight: bold;
    font-size: 0.9rem;
  }

  @media (max-width: 1024px) {
    margin: 0.5rem 0 0.5rem 0;
    font-size: 0.8rem;
  }
`;

const CommentIcon = styled.img`
  filter: opacity(0.6);
  width: 1.8rem;
  height: 1.8rem;
  margin-bottom: 0.1rem;

  @media (max-width: 1024px) {
    width: 1.5rem;
    height: 1.5rem;
  }
`;

const CommentButton = styled.button`
  background-color: skyblue;
  border: 0px;
  width: 6rem;
  height: 2.2rem;
  cursor: pointer;
  color: black;
  border-radius: 8px;
  font-size: 0.9rem;
  margin: 0 0.5rem 0.5rem 0;

  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 600;

  &:hover {
  background-color: #7cc6e3;
  }
`;