import React, { useEffect, useState, useContext } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import { getDatabase, ref, get, update } from "firebase/database";
import { getStorage, ref as storageRef, uploadBytes, deleteObject, getDownloadURL } from "firebase/storage";
import { AuthContext } from "../firebase/AuthContext ";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import styled from "styled-components";

const PortfolioEdit = () => {
//   const { id } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState(null);
  const [title, setTitle] = useState("");
  const [short, setShort] = useState("");
  const [projectDate, setProjectDate] = useState("");
  const [personNum, setPersonNum] = useState("");
  const [skils, setSkils] = useState("");
  const [content, setContent] = useState("");
  const [category, setCategory] = useState("Portfolio");
  const [preview, setPreview] = useState(false);
  const [file, setFile] = useState(null);

  const db = getDatabase();
  const storage = getStorage();
  const { currentUser } = useContext(AuthContext);
  const location = useLocation();

  const id = location.state?.id || '';

  useEffect(() => {
    const fetchPost = async () => {
      const postRef = ref(db, "Portfolio/" + id);
      const snapshot = await get(postRef);
      if (snapshot.exists()) {
        const data = snapshot.val();
        setPost(data);
        setTitle(data.title);
        setShort(data.short);
        setProjectDate(data.projectDate);
        setPersonNum(data.personNum);
        setSkils(data.skils);
        setContent(data.content);
        setCategory(data.category);
      } else {
        console.error("   .");
      }
    };
    fetchPost();
  }, [id]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!title || !content) {
      alert("  .");
      return;
    }

    let fileURL = post?.fileURL; //   URL 

    if (file) {
      //     
      if (fileURL) {
        const oldFileRef = storageRef(storage, fileURL);
        await deleteObject(oldFileRef);
      }

      //   
      const newFileRef = storageRef(storage, `uploads/${file.name}`);
      await uploadBytes(newFileRef, file);
      fileURL = await getDownloadURL(newFileRef); //   URL 
    }

    const updatedPost = {
      title,
      short,
      content,
      category,
      fileURL: fileURL || null,
      updatedAt: Date.now(),
      editTime: new Date().toLocaleString(),
      projectDate,
      personNum,
      skils,
    };

    const postRef = ref(db, "Portfolio/" + id);
    await update(postRef, updatedPost);

    alert(" !");
    navigate(`/portfolio`); //      
  };

  if (!post) {
    return <p>  ...</p>;
  }

  return (
    <div style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
      <Title> </Title>
      <hr style={{width:'95%'}}/>
      {currentUser ?
      <form onSubmit={handleSubmit} style={{width:'95%'}}>

          <CustomDiv style={{flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
          <div style={{textAlign:'left'}}>
            <SmallTitle></SmallTitle>
            <CategorySelect value={category} onChange={(e) => setCategory(e.target.value)}>
              <option value="Portfolio"></option>
            </CategorySelect>
          </div>
          <div>
            <SmallTitle></SmallTitle>
            <CheckboxInput
              type="checkbox"
              checked={preview}
              onChange={(e) => {setPreview(e.target.checked)}}
            />
          </div>
        </CustomDiv>

        <CustomDiv>
          <SmallTitle></SmallTitle>
          <TitleInput
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle> </SmallTitle>
          <TitleInput
            type="text"
            value={short}
            onChange={(e) => setShort(e.target.value)}
            placeholder="  "
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle> </SmallTitle>
          <TitleInput
            type="text"
            value={projectDate}
            onChange={(e) => setProjectDate(e.target.value)}
            placeholder="  (ex. 2024.12 / 2024.12 - 2025.02)"
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle> </SmallTitle>
          <TitleInput
            type="text"
            value={personNum}
            onChange={(e) => setPersonNum(e.target.value)}
            placeholder="1,   / 3,  "
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle> </SmallTitle>
          <TitleInput
            type="text"
            value={skils}
            onChange={(e) => setSkils(e.target.value)}
            placeholder="React, React-Native, Typescript, Styled-Components, Css, HTML, JS, Firebase"
            required
          />
        </CustomDiv>
        
        <CustomDiv>
          <SmallTitle></SmallTitle>
          <ContentTextArea
            value={content}
            onChange={(e) => setContent(e.target.value)}
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle></SmallTitle>
          <FileInput type="file" onChange={(e) => setFile(e.target.files[0])} />
        </CustomDiv>
        {post.fileURL && (
          <div>
            <p> : <a href={post.fileURL} target="_blank" rel="noopener noreferrer"> </a></p>
          </div>
        )}
        <CustomDiv style={{alignItems:'flex-end'}}>
          <PostButton type="submit"> </PostButton>
        </CustomDiv>
        {preview && 
              <PreviewContainer>
                <Title></Title>
                <hr />
                <PreviewContent>
                  <ReactMarkdown children={content} remarkPlugins={[remarkGfm]} />
                </PreviewContent>
              </PreviewContainer>
              }
      </form>
      :
      <div>
        <h3 style={{color: 'red'}}>!     !</h3>
      </div>
      }
    </div>
  );
};

export default PortfolioEdit;

const CustomDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const CategorySelect = styled.select`
  margin: 0.5rem 0 0 0.5rem;
  width: 10rem;
  height: 1.5rem;
`;

const Title = styled.p`
  font-family: "Yeon Sung", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 2.5rem;
  margin: 1rem 0 0.5rem 0;
`;

const SmallTitle = styled.p`
  font-family: "Yeon Sung", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.5rem;
  margin: 1rem 0 0 0.5rem;
`;

const CheckboxInput = styled.input`
  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;
`;

const TitleInput = styled.input`
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  height: 1.5rem;
  // background-color: lightgray;
`;

const ContentTextArea = styled.textarea`
  resize: none;
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  height: 20rem;
`;

const FileInput = styled.input`
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  // height: 1.5rem;
`;

const PostButton = styled.button`
  background-color: skyblue;
  border: 0px;
  width: 6rem;
  height: 2.2rem;
  cursor: pointer;
  color: black;
  border-radius: 8px;
  font-size: 0.9rem;
  margin: 0 1rem 0 0;

  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 600;

  &:hover {
  background-color: #7cc6e3;
  }
`;

const PreviewContainer = styled.div`
  width: calc(90% - 0.5rem);
  margin: 2rem 0 5rem 0.5rem;
`

const PreviewContent = styled.p`
  font-size: 1.2rem;
  text-align: left;
`;