import React, { useState, useContext } from "react";
import { getDatabase, ref, push } from "firebase/database";
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from "firebase/storage";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "./AuthContext ";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import styled from "styled-components";

const Post = () => {
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [category, setCategory] = useState("");
  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(false);
  const [previewURL, setPreviewURL] = useState("");

  const { currentUser } = useContext(AuthContext); //    
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!title || !content) {
      alert("  .");
      return;
    }

    const db = getDatabase();
    const postsRef = ref(db, "posts");
    const storage = getStorage();

    let fileURL = null;

    if (file) {
      const fileRef = storageRef(storage, `uploads/${file.name}`);
      await uploadBytes(fileRef, file);
      fileURL = await getDownloadURL(fileRef);
    };
    const newPost = {
      title,
      content,
      category,
      fileURL,
      createdAt: Date.now(),
      uploadTime : new Date().toLocaleString(),
      views: 0,
      // editTime: 'None',
    };

    await push(postsRef, newPost);

    alert(" !");
    navigate("/list"); //    
  };

  const handleFileChange = async (e) => {
    const selectedFile = e.target.files[0];
  
    if (selectedFile) {
      setFile(selectedFile);
  
      // Firebase Storage  
      const storage = getStorage();
      const fileRef = storageRef(storage, `uploads/${selectedFile.name}`);
      await uploadBytes(fileRef, selectedFile);
  
      //   URL 
      const url = await getDownloadURL(fileRef);
  
      // URL  
      setPreviewURL((prevContent) => `${prevContent}\nURL : ${url}`);
      alert(" !  .");
    }
  };
  

  return (
    currentUser ?
    <div style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
      <Title> </Title>
      <hr style={{width:'95%'}}/>
      <form onSubmit={handleSubmit} style={{width:'95%'}}>

        <CustomDiv style={{flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
          <div>
          <SmallTitle></SmallTitle>
          <CategorySelect value={category} onChange={(e) => setCategory(e.target.value)}>
            <option value=""></option>
            <option value="React">React</option>
            <option value="React-Native">React Native</option>
            <option value=""></option>
          </CategorySelect>
          </div>

          <div>
          <SmallTitle></SmallTitle>
          <CheckboxInput
            type="checkbox"
            checked={preview}
            onChange={(e) => {setPreview(e.target.checked)}}
          />
          </div>
        </CustomDiv>

        <CustomDiv>
          <SmallTitle></SmallTitle>
          <TitleInput
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder=" "
            required
          />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle></SmallTitle>
            <ContentTextArea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="MarkDown   ."
              required
            />
        </CustomDiv>

        <CustomDiv>
          <SmallTitle> </SmallTitle>
          <div style={{display:'flex', width:'100%', flexDirection:'row', alignItems:'center', justifyContent:'space-between'}}>
          <FileInput type="file" onChange={handleFileChange} />
          <PostButton type="submit"> </PostButton>
          </div>
        </CustomDiv>

        <SmallTitle style={{textAlign:'left'}}> </SmallTitle>
        <CustomDiv style={{backgroundColor:'lightgray', width:'calc(100% - 1.1rem)', margin:'0.5rem'}}>
          <FileLink>{previewURL}</FileLink>
        </CustomDiv>
        
      </form>

      {preview && 
      <PreviewContainer>
        <Title></Title>
        <hr />
        <PreviewContent>
          <ReactMarkdown children={content} remarkPlugins={[remarkGfm]} />
        </PreviewContent>
      </PreviewContainer>
      }
    </div>
    :
    <div>
      <h1> </h1>
      <h3 style={{color: 'red'}}>!     !</h3>
    </div>
  );
};

export default Post;

const CustomDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const CategorySelect = styled.select`
  margin: 0.5rem 0 0 0.5rem;
  width: 10rem;
  height: 1.5rem;
`;

const CheckboxInput = styled.input`
  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;
`;

const Title = styled.p`
  font-family: "Yeon Sung", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 2.5rem;
  margin: 1rem 0 0.5rem 0;
`;

const SmallTitle = styled.p`
  font-family: "Yeon Sung", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.5rem;
  margin: 1rem 0 0 0.5rem;
`;

const TitleInput = styled.input`
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  height: 1.5rem;
  // background-color: lightgray;
`;

const PreviewContainer = styled.div`
  width: calc(90% - 0.5rem);
  margin: 2rem 0 5rem 0.5rem;
`

const ContentTextArea = styled.textarea`
  resize: none;
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  height: 20rem;
`;

const FileLink = styled.p`
  margin: 0 0 0 0.3rem;
  font-size: 1rem;
`;

const FileInput = styled.input`
  margin: 0.5rem 0 0 0.5rem;
  width: calc(100% - 1.5rem);
  // height: 1.5rem;
`;

const PostButton = styled.button`
  background-color: skyblue;
  border: 0px;
  width: 6rem;
  height: 2.2rem;
  cursor: pointer;
  color: black;
  border-radius: 8px;
  font-size: 0.9rem;
  margin: 0 1rem 0 0;

  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 600;

  &:hover {
  background-color: #7cc6e3;
  }
`;

const PreviewContent = styled.p`
  font-size: 1.2rem;
  text-align: left;
`;