import React, { useState, useEffect } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { getDatabase, ref, onValue } from "firebase/database";
import styled from "styled-components";
import { marked } from "marked"; //markdown html   
// import { Helmet } from "react-helmet";

const List = () => {
  const [posts, setPosts] = useState([]);

  const location = useLocation();
  const navigate = useNavigate();

  const category = location.state?.category || '';

  useEffect(() => {
    const db = getDatabase();
    const postsRef = ref(db, "posts");

    const unsubscribe = onValue(postsRef, (snapshot) => {
      const data = snapshot.val();
      console.log('data : ', data)
      const postsArray = data
        ? Object.keys(data).map((key) => ({ id: key, ...data[key] }))
        .filter((post) => validatePost(post)) //   
        : [];
      setPosts(postsArray.reverse()); //  (  ,   )
    });

    return () => unsubscribe();
  }, []);

  //     (    )
  const validatePost = (post) => {
    return (
      typeof post.title === "string" && post.title.trim() !== "" &&
      typeof post.content === "string" && post.content.trim() !== "" &&
      typeof post.category === "string" && post.category.trim() !== ""
    );
  };

  const printListHeader = () =>{
    const Description = () =>{
      if (category == '') return <DescriptionText>   </DescriptionText> 
      else if (category == '') return <DescriptionText> </DescriptionText> 
      else if (category == 'React') return <DescriptionText>React   </DescriptionText> 
      else if (category == 'React-Native') return <DescriptionText>RN   </DescriptionText> 
      else return <DescriptionText></DescriptionText>
    };

    return (
    <div style={{width:'95%', display:'flex', flexDirection:'column', alignItems:'start'}}>
      <CategoryText>
        {category}
      </CategoryText>
      {Description()}
    </div>
    );
  };

  const printList = (post) => {
    const stripMarkdown = (markdown) => { //    
      const html = marked.parse(markdown); //Markdown  HTML  
      const tempDiv = document.createElement("div"); // div .
      tempDiv.innerHTML = html; // div    HTML 
      return tempDiv.textContent || tempDiv.innerText || ""; // div      //textContent     innerText ,     
    }; //textContent: HTML     . //innerText:     (<h4 style="display:none"> </h4>    ).
    
    const previewText = stripMarkdown(post.content);

    return (
      <div style={{display:'flex', flexDirection:'column'}}>

        <ListButton onClick={() => {navigate(`/list/${post.id}`, {state : {sortedPosts: posts}})}} >
          <Title>{post.title}</Title>
        </ListButton>

        <Content>
          <p style={{ margin: 0 }}>{previewText}</p>
        </Content>

        <Tail>
          <TailIcon alt="folder" src={`${process.env.PUBLIC_URL}/img/folder.png`}/> {post.category} &nbsp;&nbsp;&nbsp;
          <TailIcon alt="date" src={`${process.env.PUBLIC_URL}/img/date.png`}/> {(post.uploadTime).split(' ')[0]}
        </Tail>

        <hr style={{width: '100%', margin:'0.3rem 0 0 0'}}/>
        
      </div>
    );
  };

  return (
    <div style={{display:'flex', flexDirection:'column', alignItems:'center', minHeight: 'calc(100vh - 15rem)'}}>
      {printListHeader()}

      {posts.map((post) => (
        <div key={post.id} style={{width:'95%'}}>
            {/* <Link to={`/post/${post.id}`}> */}

          {category ==='' ? //category  ,    
            printList(post)
            :
            post.category === category && //category   , post.category category   
            printList(post)
          }
        </div>
      ))}

    </div>
  );
};

export default List;

const ListButton = styled.button`
  flex-direction: column;
  height: auto;
  background-color: white;
  border: 0px;
  margin: 0;
  cursor: pointer;

  display: -webkit-box; /*    */
  -webkit-line-clamp: 1; /*  1  */
  -webkit-box-orient: vertical; /*    */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /*     */

  &:hover {
  text-decoration: underline;
  color: #2A408E;
  }
`;

const CategoryText = styled.div`
  font-family: "Yeon Sung", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 2.5rem;
  margin: 0 0 0.3rem 0;

  @media (max-width: 1024px) {
    font-size: 2.2rem;
  }
`;

const DescriptionText = styled.div`
  background-color: lightgray;
  color: gray; 
  width: calc(100% - 1.4rem); 
  text-align: left;
  padding: 0.7rem;

  font-family: "Nanum Pen Script", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.7rem;
  margin: 0 0 0.7rem 0;

  @media (max-width: 1024px) {
    font-size: 1.5rem;
  }
`;

const Content = styled.div`
  display: -webkit-box; /*    */
  -webkit-line-clamp: 2; /*  2  */
  -webkit-box-orient: vertical; /*    */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /*     */
  background-color: white;
  text-align: left;
  margin: 0.3rem 0 0.3rem 0.2rem;

  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: <weight>;
  font-style: normal;
  font-size: 1rem;

  @media (max-width: 1024px) {
    font-size: 0.8rem;
  }
`;

const Title = styled.p`
  color: #2A408E;
  margin: 0.2rem 0.2rem 0.2rem -0.2rem; 
  text-align: left;
  font-size: 1.5rem;

  @media (max-width: 1024px) {
    font-size: 1.3rem;
  }
`;

const Tail = styled.p`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin:  0.1rem 0 0 0.2rem; 
  color: gray; 
  font-weight: bold;
  font-size: 0.9rem;

  font-family: "Song Myung", serif;
  font-style: normal;

  @media (max-width: 1024px) {
    font-size: 0.7rem;
  }
`;

const TailIcon = styled.img`
  filter: opacity(0.6);
  width: 1.2rem;
  height: 1.2rem;
  margin: 0.4rem 0.1rem 0.4rem 0;

  @media (max-width: 1024px) {
    width: 0.9rem;
    height: 0.9rem;
  }
`;